/*
	Le Petit Garage
*/

/* Chez Marionnaud, on est contraint à la typo 'Sofia pro' présente sur leur site.
(la récupérer en local pour le développement mais ne pas l'inclure en CSS CF SPECS)
Il y a un mixin bold, un light, et par défaut la font est utilisée sur .lpg
*/

//// Variables
@mixin bold {
	font-family: "Sofia Pro Bold", "Sofia Pro", Arial, sans-serif;
	font-weight: bold;
}

@mixin light {
	font-family: "Sofia Pro Light", "Sofia Pro", Arial, sans-serif;
	font-weight: 300;
}
$font-text: "Sofia Pro Regular", "Sofia Pro", "sofia-pro", Helvetica, Arial, sans-serif;

$pink-marionnaud: #E0136A;

@mixin black-italic {
	font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif;
	font-weight: 900;
	font-style: italic;
}

