/* Le bouton est lun lien <a> */
.lpg-button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	min-width: 165px;
	padding: 12px 30px;
	border: 1px solid black;
	font-size: 16px;
	text-decoration: none;
	color: black;
	background: white;
	transition: all 0.2s ease-out;

	&:hover,
	&:focus,
	&:active {
		border-color: #000;
		text-decoration: none;
		color: #fff;
		background-color: #000;
		cursor: pointer;
	}
}

.lpg-button-pink {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	min-width: 165px;
	padding: 12px 30px;
	border: 1px solid white;
	font-size: 16px;
	text-decoration: none;
	color: #fff;
	background: $pink-marionnaud;
	transition: all 0.2s ease-out;

	&:hover,
	&:focus,
	&:active {
		border-color: white;
		text-decoration: none;
		color: $pink-marionnaud;
		background-color: white;
		cursor: pointer;
	}
}

.lpg-button-exclusif {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	min-width: 66px;
	padding: 5px 5px;
	border: 1px solid black;
	font-size: 11px !important;
	text-decoration: none;
	color: black;
	background: white;
	transition: all 0.2s ease-out;
}