// Ribbons and other floating objects

.lpg-banner-title,
.lpg-product-picture,
.lpg-product-picture-packshot {
	position : relative;
}

.lpg-ruban01,
.lpg-ruban02,
.lpg-ruban03,
.lpg-ruban04,
.lpg-ruban05,
.lpg-ruban06,
.lpg-percent {
	position: absolute;
}


.lpg-ruban01 {
	height: 174px;
	margin: 0 4em;
	left: 0%;
	top: 50%;
	transform: translate(-50%, -50%);
	z-index: 6;
	@media (max-width: 680px) {
		margin: 1em;
	}
	img {
		height: 100%;
		width: auto;
		@media (max-width: 680px) {
			height: 50%;
		}
	}
}

.lpg-ruban02 {
	height: 86px;
	margin: 0 4em;
	left: 0%;
	top: 0%;
	transform: translate(-100%, -50%);
	@media (max-width: 680px) {
		margin: 1em;
	}
	img {
		height: 100%;
		width: auto;
		@media (max-width: 680px) {
			height: 50%;
		}
	}
}

.lpg-ruban03 {
	height: 122px;
	left: 0%;
	top: 0%;
	transform: translate(-45%, -30%);
	img {
		height: 100%;
		width: auto;
	}
}

.lpg-ruban04 {
	height: 54px;
	right: 0;
	top: 0%;
	transform: translate(+50%,-50%);
	img {
		height: 100%;
		width: auto;
	}
}

.lpg-ruban05 {
	height: 44px;
	left: 0;
	top: 50%;
	transform: translate(-50%,-50%);
	img {
		height: 100%;
		width: auto;
	}
}

.lpg-ruban06 {
	height: 46px;
	right: 0;
	bottom: 0;
	transform: translate(50%,50%);
	img {
		height: 100%;
		width: auto;
	}
}

.lpg-percent {
	height: 25px;
	right: 0;
	top: 38%;
	transform: translate(50%,50%);
	img {
		height: 100%;
		width: auto;
	}
}