.lpg {
	h2,
	h3,
	h4,
	p, 
	button {
		font-family: $font-text;
	}
	a {
		text-decoration: none !important;
	}
	p {
		font-size: 18px;
		text-align: center;
	}
	h1 {
        @include black-italic;
        font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif ;
        font-size: 35px;
        color:#EB0F42;
        align-content: center;
        margin: 1em auto 0;
    }
        
	h3 {
		@include black-italic;
		font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif ;
		font-size: 26px;
		//width: 260px;
		align-content: center;
		margin: auto ;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		@media (max-width: 680px) {
			font-size: 18px;
		}
	}

	h4 {
		font-size: 13px;
		font-weight: lighter;
		margin: 6px auto 19px;
	}

	.lpg-intro-p p {
		margin: 1em 6em 1em;
		@media (max-width: 680px) {
			margin: 1em 2em 1em;
		}
	}

	.lpg-pink-banner p {
		margin: 1.5em 9em 1.5em;
		font-size: 18px;
		color: white;
		@media (max-width: 680px) {
			margin: 2em 2em 1em;
		}
	}

	.lpg-product-offer {
        margin: 0;
        font-size: 16px;
        color: #EB0F42;
    }

	.lpg-product-name {
		margin: 0;
		font-size: 16px;
	}

	.lpg-product-tagline {
		padding: 0 10px;
		margin: 0;
		font-size: 20px;
		font-weight: bold;
	}

	.lpg-exclusif-div {
		margin: 20px auto 15px;
		min-height: 29px;
	}

	.lpg-pink-text {
        color: #EB0F42;
    }


	.lpg-product-name-div {
		margin: 25px auto 19px;
		h4 {
			padding: 1em;
			font-size: 14px;
		}
	}

	.two-lines {
		min-height: 54px;
	}

	.three-lines {
		min-height: 80px;
	}

}

