/**
    Ligne Flex structure 2 colonnes
**/
.lpg-row {
	display: flex;
	align-items: center;
	justify-content: center;
	@media (max-width: 540px) {
		flex-flow: row wrap;
	}
}

.lpg-col {
	width: 50%;

	@media (max-width: 540px) {
		width: 100%;
	}
}

/* pictures styles */
.lpg-smiley {
	height: 29px;
	width: auto;
	padding: 0;
	margin: 0 auto;
	img {
		height: 29px;
	}
}

.lpg-product-picture {
	display: block;
	margin: 0 auto;
	height: 210px;
	width: 135px;
	img {
		height: 210px;
		width: 135px;
	}
}

.lpg-product-picture-packshot {
	display: block;
	margin: 0 auto;
	height: 260px;
	width: 187px;
	img {
		height: 260px;
		width: 187px;
	}
}

.lpg-square-icons {
	display: block;
	margin: 0 auto;
	
	
	img {
	height: 189px; 
	width: auto;
	}
}

.lpg-product-row {
	width: 100%;
	padding-bottom: 60px;
}



/* grid parameters */
.lpg-wrapper {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 0;
	grid-auto-rows: minmax(100px, auto);
	width: 100%;
	@media (max-width: 770px) {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: 380px) {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
	}
}

.lpg-product {
	margin: 0 auto;
}

.lpg-product-1 {
	grid-column: 1;
	grid-row: 1;
}

.lpg-product-2 {
	grid-column: 2;
	grid-row: 1;
	@media (max-width: 380px) {
		grid-column: 1;
		grid-row: 2;
	}
}

.lpg-product-3 {
	grid-column: 3;
	grid-row: 1;
	@media (max-width: 770px) {
		grid-column: 1;
		grid-row: 2;
	}
	@media (max-width: 380px) {
		grid-column: 1;
		grid-row: 3;
	}
}

.lpg-product-4 {
	grid-column: 4;
	grid-row: 1;
	@media (max-width: 770px) {
		grid-column: 2;
		grid-row: 2;
	}
	@media (max-width: 380px) {
		grid-column: 1;
		grid-row: 4;
	}
}


.lpg-brand-row {
	margin: 1em;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;

	div {
		margin: 7px;
		width: 132px;

	}

	
}

.lpg-stars {
	img {
		max-width: 15px;
	}
}



