.lpg {
	width: 100%;
	background-color: #fff;
	margin: 0 auto;
	font-family: $font-text;
	font-size: 18px;
	line-height: 1.3333;
	color: #000;
	max-width: 1024px;
	text-align: center;

	/* Reset interne LPG */
	img {
		width: auto;
		max-width: 100%;
		height: auto;
		border: 0;
		vertical-align: bottom;
	}

	* {
		box-sizing: border-box;
	}
}
