@charset "UTF-8";
/*
	Le Petit Garage
*/
/* Chez Marionnaud, on est contraint à la typo 'Sofia pro' présente sur leur site.
(la récupérer en local pour le développement mais ne pas l'inclure en CSS CF SPECS)
Il y a un mixin bold, un light, et par défaut la font est utilisée sur .lpg
*/
.lpg {
  width: 100%;
  background-color: #fff;
  margin: 0 auto;
  font-family: "Sofia Pro Regular", "Sofia Pro", "sofia-pro", Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.3333;
  color: #000;
  max-width: 1024px;
  text-align: center;
  /* Reset interne LPG */ }
  .lpg img {
    width: auto;
    max-width: 100%;
    height: auto;
    border: 0;
    vertical-align: bottom; }
  .lpg * {
    box-sizing: border-box; }

.lpg h2,
.lpg h3,
.lpg h4,
.lpg p,
.lpg button {
  font-family: "Sofia Pro Regular", "Sofia Pro", "sofia-pro", Helvetica, Arial, sans-serif; }

.lpg a {
  text-decoration: none !important; }

.lpg p {
  font-size: 18px;
  text-align: center; }

.lpg h1 {
  font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif;
  font-weight: 900;
  font-style: italic;
  font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif;
  font-size: 35px;
  color: #EB0F42;
  align-content: center;
  margin: 1em auto 0; }

.lpg h3 {
  font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif;
  font-weight: 900;
  font-style: italic;
  font-family: "Sofia Pro Black Italic", sofia-pro, sans-serif;
  font-size: 26px;
  align-content: center;
  margin: auto;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); }
  @media (max-width: 680px) {
    .lpg h3 {
      font-size: 18px; } }

.lpg h4 {
  font-size: 13px;
  font-weight: lighter;
  margin: 6px auto 19px; }

.lpg .lpg-intro-p p {
  margin: 1em 6em 1em; }
  @media (max-width: 680px) {
    .lpg .lpg-intro-p p {
      margin: 1em 2em 1em; } }

.lpg .lpg-pink-banner p {
  margin: 1.5em 9em 1.5em;
  font-size: 18px;
  color: white; }
  @media (max-width: 680px) {
    .lpg .lpg-pink-banner p {
      margin: 2em 2em 1em; } }

.lpg .lpg-product-offer {
  margin: 0;
  font-size: 16px;
  color: #EB0F42; }

.lpg .lpg-product-name {
  margin: 0;
  font-size: 16px; }

.lpg .lpg-product-tagline {
  padding: 0 10px;
  margin: 0;
  font-size: 20px;
  font-weight: bold; }

.lpg .lpg-exclusif-div {
  margin: 20px auto 15px;
  min-height: 29px; }

.lpg .lpg-pink-text {
  color: #EB0F42; }

.lpg .lpg-product-name-div {
  margin: 25px auto 19px; }
  .lpg .lpg-product-name-div h4 {
    padding: 1em;
    font-size: 14px; }

.lpg .two-lines {
  min-height: 54px; }

.lpg .three-lines {
  min-height: 80px; }

/**
    Ligne Flex structure 2 colonnes
**/
.lpg-row {
  display: flex;
  align-items: center;
  justify-content: center; }
  @media (max-width: 540px) {
    .lpg-row {
      flex-flow: row wrap; } }

.lpg-col {
  width: 50%; }
  @media (max-width: 540px) {
    .lpg-col {
      width: 100%; } }

/* pictures styles */
.lpg-smiley {
  height: 29px;
  width: auto;
  padding: 0;
  margin: 0 auto; }
  .lpg-smiley img {
    height: 29px; }

.lpg-product-picture {
  display: block;
  margin: 0 auto;
  height: 210px;
  width: 135px; }
  .lpg-product-picture img {
    height: 210px;
    width: 135px; }

.lpg-product-picture-packshot {
  display: block;
  margin: 0 auto;
  height: 260px;
  width: 187px; }
  .lpg-product-picture-packshot img {
    height: 260px;
    width: 187px; }

.lpg-square-icons {
  display: block;
  margin: 0 auto; }
  .lpg-square-icons img {
    height: 189px;
    width: auto; }

.lpg-product-row {
  width: 100%;
  padding-bottom: 60px; }

/* grid parameters */
.lpg-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0;
  grid-auto-rows: minmax(100px, auto);
  width: 100%; }
  @media (max-width: 770px) {
    .lpg-wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr); } }
  @media (max-width: 380px) {
    .lpg-wrapper {
      display: grid;
      grid-template-columns: repeat(1, 1fr); } }

.lpg-product {
  margin: 0 auto; }

.lpg-product-1 {
  grid-column: 1;
  grid-row: 1; }

.lpg-product-2 {
  grid-column: 2;
  grid-row: 1; }
  @media (max-width: 380px) {
    .lpg-product-2 {
      grid-column: 1;
      grid-row: 2; } }

.lpg-product-3 {
  grid-column: 3;
  grid-row: 1; }
  @media (max-width: 770px) {
    .lpg-product-3 {
      grid-column: 1;
      grid-row: 2; } }
  @media (max-width: 380px) {
    .lpg-product-3 {
      grid-column: 1;
      grid-row: 3; } }

.lpg-product-4 {
  grid-column: 4;
  grid-row: 1; }
  @media (max-width: 770px) {
    .lpg-product-4 {
      grid-column: 2;
      grid-row: 2; } }
  @media (max-width: 380px) {
    .lpg-product-4 {
      grid-column: 1;
      grid-row: 4; } }

.lpg-brand-row {
  margin: 1em;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }
  .lpg-brand-row div {
    margin: 7px;
    width: 132px; }

.lpg-stars img {
  max-width: 15px; }

.lpg-banner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: white; }
  .lpg-banner__img {
    width: 100%;
    max-width: 224px;
    padding: 25px 0 30px; }
    @media (max-width: 680px) {
      .lpg-banner__img {
        max-width: 160px;
        padding: 20px 0 20px; } }

.lpg-pink-banner {
  display: flex;
  flex-direction: column;
  margin-bottom: 65px;
  align-items: center;
  justify-content: center;
  padding: 1em 1em 2em;
  min-height: 250px;
  width: 100%;
  background-color: #E0136A; }

.lpg-banner-title {
  margin: 0 4em;
  min-height: 122px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }

/* Le bouton est lun lien <a> */
.lpg-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 165px;
  padding: 12px 30px;
  border: 1px solid black;
  font-size: 16px;
  text-decoration: none;
  color: black;
  background: white;
  transition: all 0.2s ease-out; }
  .lpg-button:hover, .lpg-button:focus, .lpg-button:active {
    border-color: #000;
    text-decoration: none;
    color: #fff;
    background-color: #000;
    cursor: pointer; }

.lpg-button-pink {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 165px;
  padding: 12px 30px;
  border: 1px solid white;
  font-size: 16px;
  text-decoration: none;
  color: #fff;
  background: #E0136A;
  transition: all 0.2s ease-out; }
  .lpg-button-pink:hover, .lpg-button-pink:focus, .lpg-button-pink:active {
    border-color: white;
    text-decoration: none;
    color: #E0136A;
    background-color: white;
    cursor: pointer; }

.lpg-button-exclusif {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 66px;
  padding: 5px 5px;
  border: 1px solid black;
  font-size: 11px !important;
  text-decoration: none;
  color: black;
  background: white;
  transition: all 0.2s ease-out; }

/*
    Texte centré
*/
.lpg-center-text {
  width: 100%;
  padding: 30px 20px;
  text-align: center; }

.lpg-banner-title,
.lpg-product-picture,
.lpg-product-picture-packshot {
  position: relative; }

.lpg-ruban01,
.lpg-ruban02,
.lpg-ruban03,
.lpg-ruban04,
.lpg-ruban05,
.lpg-ruban06,
.lpg-percent {
  position: absolute; }

.lpg-ruban01 {
  height: 174px;
  margin: 0 4em;
  left: 0%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 6; }
  @media (max-width: 680px) {
    .lpg-ruban01 {
      margin: 1em; } }
  .lpg-ruban01 img {
    height: 100%;
    width: auto; }
    @media (max-width: 680px) {
      .lpg-ruban01 img {
        height: 50%; } }

.lpg-ruban02 {
  height: 86px;
  margin: 0 4em;
  left: 0%;
  top: 0%;
  transform: translate(-100%, -50%); }
  @media (max-width: 680px) {
    .lpg-ruban02 {
      margin: 1em; } }
  .lpg-ruban02 img {
    height: 100%;
    width: auto; }
    @media (max-width: 680px) {
      .lpg-ruban02 img {
        height: 50%; } }

.lpg-ruban03 {
  height: 122px;
  left: 0%;
  top: 0%;
  transform: translate(-45%, -30%); }
  .lpg-ruban03 img {
    height: 100%;
    width: auto; }

.lpg-ruban04 {
  height: 54px;
  right: 0;
  top: 0%;
  transform: translate(50%, -50%); }
  .lpg-ruban04 img {
    height: 100%;
    width: auto; }

.lpg-ruban05 {
  height: 44px;
  left: 0;
  top: 50%;
  transform: translate(-50%, -50%); }
  .lpg-ruban05 img {
    height: 100%;
    width: auto; }

.lpg-ruban06 {
  height: 46px;
  right: 0;
  bottom: 0;
  transform: translate(50%, 50%); }
  .lpg-ruban06 img {
    height: 100%;
    width: auto; }

.lpg-percent {
  height: 25px;
  right: 0;
  top: 38%;
  transform: translate(50%, 50%); }
  .lpg-percent img {
    height: 100%;
    width: auto; }
