.lpg-banner {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	background-color: white;

	&__img {
		width: 100%;
		max-width: 224px;
		padding: 25px 0 30px;
		@media (max-width: 680px) {
			max-width: 160px;
			padding: 20px 0 20px;
		}
	}
}

.lpg-pink-banner {
	display: flex;
	flex-direction: column;
	margin-bottom: 65px;
	align-items: center;
	justify-content: center;
	padding: 1em 1em 2em;
	min-height: 250px;
	width: 100%;
	background-color: $pink-marionnaud;
}


.lpg-banner-title {
   // background-image: url(assets/img/marionnaud_hp_beauty_hit_1020.jpg);
   margin: 0 4em;
   min-height: 122px;
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;


}